import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '../store'

const axios = require('axios')
const base = window.location.protocol + '//' + window.location.host

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: '404',
    component: () => import('../views/NotFound.vue'),
  },
  {
    path: '/',
    name: 'Main',
    component: () => import('../views/Main.vue'),
  },
  {
    path: '/trading',
    name: 'Trading',
    component: () => import('../views/Trading.vue'),
  },
  {
    path: '/wallet',
    name: 'Wallet',
    component: () => import('../views/Wallet.vue'),
  },
  {
    path: '/contract',
    name: 'Contract',
    component: () => import('../views/Contract.vue'),
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue'),
  },
  {
    path: '/account',
    name: 'Profile',
    component: () => import('../views/Profile.vue'),
  },
  {
    path: '/terms',
    name: 'Terms',
    component: () => import('../views/Terms.vue'),
  },
  {
    path: '/trade-guard/contract',
    name: 'TradeGuard',
    component: () => import('../views/TradeGuard.vue'),
  },
]

/* eslint-disable */
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

router.beforeEach((to, from, next) => {
  if (to.name == 'Terms') {
    axios
      .get(base + '/api/user/terms', {
        headers: {
          Authorization: localStorage.getItem('auth-token'),
        },
      })
      .then(response => {
        store.commit('auth/SET_TERMS', response.data)
      })
      .catch(() => {})
  } else if (to.name != 'Main') {
    document.body.style.overflowY = 'auto'
  } else {
    if (Vue.prototype.$isDesktop) document.body.style.overflowY = 'hidden'
  }

  if (to.path === '/reset' && to.query.token) {
    store.commit('auth/RESET_PASSWORD', to.query.token)
    router.push({ path: '/' })
  }

  if (to.name == 'TradeGuard' && !store.getters['guard/CONTRACT']) {
    axios
      .post(base + '/trade-guard/connect', {
        pin: to.query.pin,
      })
      .then(response => {
        store.commit('guard/SET_CONTRACT', response.data)
        if (to.name != from.name) {
          store.dispatch('preloader/startBeforeLoading').then(next)
        }
      })
      .catch(() => {
        router.push({ path: '/wallet' })
      })
  } else if (to.name != from.name) {
    store.dispatch('preloader/startBeforeLoading').then(next)
  }
})

router.beforeResolve((to, from, next) => {
  if (to.name == 'TradeGuard' && !store.getters['guard/CONTRACT']) {
    router.push({ path: '/wallet' })
  } else if (store.getters['auth/profile']) {
    next()
  } else {
    if (!localStorage.getItem('auth-token')) {
      if (!store.state.auth.isDemo) {
        // REMOVE ON PRODUCTION
        if (['Profile', 'Wallet'].includes(to.name)) {
          router.push({ path: '/' })
          store.commit('popups/SIGN_IN', true)
        }

        localStorage.removeItem('auth-token')
      }

      next()
      return
    }

    axios
      .get(base + '/api/auth', {
        headers: {
          Authorization: localStorage.getItem('auth-token'),
          route: to.name,
        },
        maxContentLength: Infinity,
        maxBodyLength: Infinity,
      })
      .then(({ data }) => {
        store.dispatch('auth/getAuthorized', {
          ...data,
          notifyAboutNewMessage: false,
        })

        next()
      })
      .catch(err => {
        if (!store.state.auth.isDemo) {
          // REMOVE ON PRODUCTION
          if (['Profile', 'Wallet'].includes(to.name)) {
            router.push({ path: '/' })
            store.commit('popups/SIGN_IN', true)
          }

          localStorage.removeItem('auth-token')
        }

        next()
      })
  }

  document.body.scrollTop = 0
})

router.afterEach((to, from, next) => {
  let host = store.getters.host
  let title = to.name != 'Main' ? host + ' - ' + to.name : host
  store.commit('SET_TITLE', title)

  if (!['Trading', 'TradeGuard'].includes(to.name)) {
    store.dispatch('preloader/startAfterLoading').then(next)
  }
})

export default router
